import LoginOther from '@/components/login/login-other.vue'
import $http from '@/common/api/request.js'
import {mapState,mapActions,mapGetters,mapMutations} from 'vuex'
export default {
	data() {
		return {
			username: "",
			password: "",
			//验证规则
			rules:{
				username:{
					rule:/\S/,
					msg:"账号不能为空"
				},
				password:{
					rule:/^[0-9a-zA-z]{6,16}$/,
					msg:"密码为6-16位字符"
				}
			}
		}
	},
	components: {
		LoginOther
	},
	methods: {
		...mapMutations(['login']),
		goBack() {
			uni.navigateBack({
				delta: 1
			})
		},
		//点击登录
		subMit(){
			if(!this.validate('username')) return;
			if(!this.validate('password')) return;
			uni.showLoading({
				title:"登录中..."
			})
			$http.request({
				url:"/login",
				method:"POST",
				data:{
					userName:this.username,
					userPwd:this.password
				}
			}).then((res)=>{
				if(res.success){
					this.login(res.data)
					uni.showToast({
						title:res.msg,
						icon:'none'
					})
					setTimeout(()=>{
						uni.hideLoading();
						uni.navigateBack({
							delta:1
						})
					},2000)
				}else{
					uni.showToast({
						title:res.msg,
						icon:'none'
					})
					// uni.hideLoading();
				}
				// 
			})
			
		},
		//验证登录
		validate(key){
			let bool=true;
			console.log(this.rules[key].rule.test(this[key]))
			if(!this.rules[key].rule.test(this[key])){
				uni.showToast({
					title:this.rules[key].msg,
					icon:"none"
				})
				bool=false;
				return false
			}
			return bool;
		},
		//手机号注册
		goLoginTel(){
			uni.navigateTo({
				url:"../login-tel/login-tel"
			})
		}
	}
}
